'use client'

import { useState } from 'react'

export default function CreateTestUsersPage() {
  const [loading, setLoading] = useState(false)
  const [result, setResult] = useState<string>('')

  const handleCreateUsers = async () => {
    setLoading(true)
    setResult('')
    
    try {
      const res = await fetch('/api/admin/create-test-users', {
        method: 'POST'
      })
      const json = await res.json()
      setResult(JSON.stringify(json, null, 2))
    } catch (error) {
      setResult(`Error: ${error}`)
    } finally {
      setLoading(false)
    }
  }

  // Only show in development
  if (process.env.NODE_ENV !== 'development') {
    return (
      <div className="text-center py-12">
        <p className="text-gray-500">This page is only available in development mode.</p>
      </div>
    )
  }

  return (
    <div className="max-w-2xl mx-auto">
      <div className="bg-white rounded-lg shadow p-6">
        <h1 className="text-2xl font-bold text-gray-900 mb-6">Create Test Users</h1>
        
          <div className="bg-yellow-50 border border-yellow-200 rounded-md p-4 mb-6">
          <h3 className="text-sm font-medium text-yellow-800">Development Tool</h3>
          <div className="mt-2 text-sm text-yellow-700">
            <p>This will create test users with complete profiles (including prominent profiles).</p>
            <ul className="list-disc list-inside mt-2 space-y-1">
              <li>3 standard users (Alice, Bob, Carol)</li>
              <li>6 prominent users (Satya, Jensen, Sam, Alex, Peter, Eric)</li>
            </ul>
          </div>
        </div>

        <button
          onClick={handleCreateUsers}
          disabled={loading}
          className="w-full bg-blue-600 text-white py-3 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {loading ? 'Creating Users...' : 'Create Test Users'}
        </button>

        {result && (
          <div className="mt-6">
            <h3 className="text-sm font-medium text-gray-900 mb-2">Result:</h3>
            <pre className="bg-gray-100 p-4 rounded-md text-sm whitespace-pre-wrap font-mono">
              {result}
            </pre>
          </div>
        )}

        <div className="mt-6 text-sm text-gray-600">
          <p><strong>After creating users:</strong></p>
          <ol className="list-decimal list-inside mt-2 space-y-1">
            <li>Visit <code className="bg-gray-100 px-1 rounded">/members</code> to see the test profiles</li>
            <li>Try clicking "Remember" on different profiles</li>
            <li>Visit <code className="bg-gray-100 px-1 rounded">/my-circle</code> to see saved connections</li>
            <li>Test the Meet opt-in functionality</li>
          </ol>
        </div>
      </div>
    </div>
  )
}
